import React, { Component } from 'react';
import { connect } from 'react-redux';

import InputField from '../customFields/inputField'
import Loader from '../customFields/loader';
import { getValue } from '../../utils/formatter';
import { isObjEmpty } from '../../utils';
import { validate } from '../../utils/validation';
import { updatePageData, getIndividualItem, resetPageStore } from '../../actions/page';
import { Endpoint } from '../../utils/constants';

import ModalButton from '../modal/button';
import Addonmodal from '../modal/addonmodal';

class IndiaofIdeas extends Component {
    constructor(props) {
        super(props);
        this.state = this.intialiseState();
    }

    intialiseState = (data = {}) => {
        return {
            title: getValue(data, 'title', ''),
            videoUrl: getValue(data, 'videoUrl', ''),
            videoThumb: getValue(data, 'videoThumb', ''),
            about: getValue(data, 'aboutAIOI', ''),
            fb: getValue(data, 'fb', ''),
            tw: getValue(data, 'tw', ''),
            insta: getValue(data, 'insta', ''),
            linkedIn: getValue(data, 'linkedIn', ''),
            videos: getValue(data, 'videos', [{ videoUrl: '', videoThumb: '', about: '' }]),
            errorMessages: {},
            isValid: {},
            videosErr: {},
            isSaving: false
        }
    }

    componentDidMount() {
        if (this.props.id) {
            this.props.getIndividualItem({
                params: { token: this.props.token },
                url: `${Endpoint.IDEAS_MANAGE}/${this.props.id}`
            });
        }
    }

    componentWillUnmount() {
        this.props.resetPageStore('individualItem');
    }

    componentDidUpdate(prevProps, prevState) {
        let { itemData, updatedData } = this.props;
        if (prevProps.itemData != itemData && !isObjEmpty(itemData)) {
            let stateD = this.intialiseState(this.props.itemData);
            this.setState(stateD);
        }
        if (updatedData != prevProps.updatedData && !isObjEmpty(updatedData) && updatedData.page == 'anioisocial') {
            let { fb = '', insta = '', tw = '', linkedIn = '', type, index } = updatedData;
            if (type == 'p') {
                this.setState({ fb, insta, tw, linkedIn });
            }
            if (type == 's') {
                let { videos } = this.state;
                videos[index] = { ...videos[index], fb, insta, tw, linkedIn }
                this.setState({ videos });
            }
            this.props.resetPageStore('updatedData');
        }
    }

    validate = (name, value) => {
        let { isValid, errorMessages } = this.state;
        let error = validate(name, value, { type: 'text', regexType: name == 'videoUrl' ? 'url' : 'noop' });
        isValid[name] = error.isValid;
        error.isValid
            ? delete errorMessages[name]
            : (errorMessages[name] = error.errorMsg);
        this.setState({ isValid, errorMessages });
    };

    inputChange = (e, id) => {
        let {
            target: { name, value, checked, type, imageUrl }
        } = e;
        if (id || id === 0) {
            let { videosErr, videos } = this.state;
            let error = validate(name, value, { type: 'text', regexType: name == 'videoUrl' ? 'url' : 'noop' });
            videosErr[id] = videosErr[id] ? videosErr[id] : {}
            error.isValid ? delete videosErr[id][name]
                : (videosErr[id][name] = error.errorMsg);
            videos[id][name] = value;
            this.setState({ videosErr, videos })
        } else {
            this.setState({ [name]: type == 'checkbox' ? checked : value }, () => {
                if(["title", "videoThumb", "about"].indexOf(name) > -1){
                    this.validate(name, value);
                }
                if(name === 'videoThumb'){
                  this.setState({imageUrl});
                }
            });
        }
    };

    onSubmit = (e) => {
        e.preventDefault();
        ["title", "videoThumb", "about"].forEach((name) => {
            this.validate(name, this.state[name]);
        });

        if (Object.keys(this.state.errorMessages).length == 0) {
            this.setState({ error: '', isSaving: true });
            let payload = Object.assign({}, this.state);
            payload.token = this.props.token
            delete payload.errorMessages;
            delete payload.isValid;
            this.props.updatePageData({
                payload,
                url: `${Endpoint.IDEAS_MANAGE}${this.props.id ? '/' + this.props.id : ''}`,
                method: this.props.id ? "PUT" : 'POST',
                action: this.props.id ? "update" : 'add',
                page: 'aoio',
                dataToUpdate:[{key: 'videoThumb', value: this.state.imageUrl}]
            }, (response) => {
                if (response.status) {
                    this.setState({ error: '', isSaving: false });
                    this.props.toastmessage(response.message);
                    this.props.closeModal();
                } else {
                    this.setState({ error: response.message, isSaving: false })
                }
            });
        }
    }

    deleteItem = (e) => {
        this.props.updatePageData({
            payload: {},
            params: { token: this.props.token },
            url: `${Endpoint.IDEAS_MANAGE}/${this.props.id}`,
            method: 'DELETE',
            action: 'delete',
            page: 'aoio'
        }, (response) => {
            if (response.status) {
                this.setState({ error: '', isSaving: false });
                this.props.toastmessage(response.message);
                this.props.closeModal();
            } else {
                this.setState({ error: response.message, isSaving: false })
            }
        });
    }

    removeId = (e, id) => {
        e.preventDefault();
        let videos = [], videosErr = {};
        if (id == this.state.videos.length - 1) {
            videos = [{ videoUrl: '', videoThumb: '', about: '' }]
            videosErr = {}
        } else {
            videos = this.state.videos.filter((v, i) => {
                return id !== i;
            });
            delete this.state.videosErr[id];
            videosErr = this.state.videosErr;
        }
        this.setState({ videos, videosErr });
    }

    addMore = () => {
        this.setState({ videos: [...this.state.videos, { videoUrl: '', videoThumb: '', about: '' }] })
    }

    render() {
        let { title, videoUrl, videoThumb, about, videos, videosErr, fb, insta, tw, linkedIn, isValid, errorMessages, isSaving } = this.state;
        let { itemLoader, id } = this.props;
        return (
            <div className='eventModal indiaofideas'>
                <p className='title-txt'>{id ? 'EDIT INDIA OF IDEAS' : 'ADD A NEW AN INDIA OF IDEAS'}</p>
                {itemLoader && <Loader />}
                {!itemLoader && <React.Fragment>
                    <div className='row'>
                        <div className='col-7'>
                            <div className='row no-gutters mb15'>
                                <div className='col-3'><label className='label-txt'>TITLE OF AIOI</label></div>
                                <div className='col-7'>
                                    <InputField name="title" groupClass="input-style" value={title} isValid={isValid.title} errorMsg={errorMessages.title}
                                        inputChange={this.inputChange} dom={{ placeholder: 'TITLE OF AIOI' }} />
                                </div>
                            </div>
                        </div>
                        <div className='col-3'><div className='btn w100per' onClick={this.addMore}>Add more videos</div></div>
                    </div>
                    <div className='uploadVideoList horizontal d-flex'>
                        <div className='listItem'>
                            <label className='label-txt'>Main Video URL</label>
                            <InputField name="videoUrl" groupClass="input-style" value={videoUrl} isValid={isValid.videoUrl} errorMsg={errorMessages.videoUrl}
                                inputChange={this.inputChange} dom={{ placeholder: 'Main Video URL' }} />
                            <label className='label-txt'>Upload Thumbnail</label>
                            <InputField name="videoThumb" groupClass="input-style" value={videoThumb} isValid={isValid.videoThumb} errorMsg={errorMessages.videoThumb}
                                inputChange={this.inputChange} section="aioi" dom={{ type: 'file', placeholder: 'Upload Thumbnail' }} >
                                <p className="input-style">{videoThumb ? videoThumb : 'Upload Thumbnail'}</p>
                            </InputField>
                            <label className='label-txt' >About</label>
                            <textarea className='input-style h100px' name="about" value={about} onChange={this.inputChange}></textarea>
                            {errorMessages.about && <p className="error_msg"> {errorMessages.about}</p>}
                            <div className='social-icon'>
                                <ModalButton
                                    className="ico-Box"
                                    data={{
                                        showCustomModal: true,
                                        addon: true,
                                        title: '',
                                        path: 'IndiaofIdeas/sharelink',
                                        body: '',
                                        data: { type: 'p', fb, tw, insta, linkedIn },
                                        className: 'modal-small'
                                    }}
                                >
                                    <span><i className="fa fa-facebook-square"></i></span>
                                </ModalButton>
                                <ModalButton
                                    className="ico-Box"
                                    data={{
                                        showCustomModal: true,
                                        addon: true,
                                        title: '',
                                        path: 'IndiaofIdeas/sharelink',
                                        body: '',
                                        data: { type: 'p', fb, tw, insta, linkedIn },
                                        className: 'modal-small'
                                    }}
                                >
                                    <span><i className="fa fa-twitter"></i></span>
                                </ModalButton>
                                <ModalButton
                                    className="ico-Box"
                                    data={{
                                        showCustomModal: true,
                                        addon: true,
                                        title: '',
                                        path: 'IndiaofIdeas/sharelink',
                                        body: '',
                                        data: { type: 'p', fb, tw, insta, linkedIn },
                                        className: 'modal-small'
                                    }}
                                >
                                    <span><i className="fa fa-instagram"></i></span>
                                </ModalButton>
                                <ModalButton
                                    className="ico-Box"
                                    data={{
                                        showCustomModal: true,
                                        addon: true,
                                        title: '',
                                        path: 'IndiaofIdeas/sharelink',
                                        body: '',
                                        data: { type: 'p', fb, tw, insta, linkedIn },
                                        className: 'modal-small'
                                    }}
                                >
                                    <span><i className="fa fa-link"></i></span>
                                </ModalButton>
                            </div>
                        </div>
                        {videos.length > 0 && videos.map((item, i) => {
                            let err = videosErr[i] ? videosErr[i] : {}
                            return (
                                <div className='listItem' key={i}>
                                    <div className='d-flex justify-content-between'>
                                        <label className='label-txt'>Video {i + 1} URL</label>
                                        {videos.length > 1 && <span className='itemDelete' onClick={(e) => this.removeId(e, i)}>+</span>}
                                    </div>
                                    <InputField groupClass="input-style" name="videoUrl" value={item.videoUrl} isValid={err.videoUrl ? false : true} errorMsg={err.videoUrl}
                                        inputChange={(e) => this.inputChange(e, i)} dom={{ placeholder: 'Video URL' }} />
                                    <label className='label-txt'>Upload Thumbnail</label>
                                    <InputField name="videoThumb" groupClass="input-style" value={item.videoThumb} isValid={err.videoThumb ? false : true} errorMsg={err.videoThumb}
                                        inputChange={(e) => this.inputChange(e, i)} section="aioi" dom={{ type: 'file', placeholder: 'Upload Thumbnail' }} >
                                        <p className="input-style">{item.videoThumb ? item.videoThumb : 'Upload Thumbnail'}</p>
                                    </InputField>
                                    <label className='label-txt'>About</label>
                                    <textarea className='input-style h100px' name="about" value={item.about} onChange={(e) => this.inputChange(e, i)}></textarea>
                                    <div className='social-icon'>
                                        <ModalButton
                                            className="ico-Box"
                                            data={{
                                                showCustomModal: true,
                                                addon: true,
                                                title: '',
                                                path: 'IndiaofIdeas/sharelink',
                                                body: '',
                                                data: { type: 's', index: i, fb: item.fb, tw: item.tw, insta: item.insta, linkedIn: item.linkedIn },
                                                className: 'modal-small'
                                            }}
                                        >
                                            <span><i className="fa fa-facebook-square"></i></span>
                                        </ModalButton>
                                        <ModalButton
                                            className="ico-Box"
                                            data={{
                                                showCustomModal: true,
                                                addon: true,
                                                title: '',
                                                path: 'IndiaofIdeas/sharelink',
                                                body: '',
                                                data: { type: 's', index: i, fb: item.fb, tw: item.tw, insta: item.insta, linkedIn: item.linkedIn },
                                                className: 'modal-small'
                                            }}
                                        >
                                            <span><i className="fa fa-twitter"></i></span>
                                        </ModalButton>
                                        <ModalButton
                                            className="ico-Box"
                                            data={{
                                                showCustomModal: true,
                                                addon: true,
                                                title: '',
                                                path: 'IndiaofIdeas/sharelink',
                                                body: '',
                                                data: { type: 's', index: i, fb: item.fb, tw: item.tw, insta: item.insta, linkedIn: item.linkedIn },
                                                className: 'modal-small'
                                            }}
                                        >
                                            <span><i className="fa fa-instagram"></i></span>
                                        </ModalButton>
                                        <ModalButton
                                            className="ico-Box"
                                            data={{
                                                showCustomModal: true,
                                                addon: true,
                                                title: '',
                                                path: 'IndiaofIdeas/sharelink',
                                                body: '',
                                                data: { type: 's', index: i, fb: item.fb, tw: item.tw, insta: item.insta, linkedIn: item.linkedIn },
                                                className: 'modal-small'
                                            }}
                                        >
                                            <span><i className="fa fa-link"></i></span>
                                        </ModalButton>
                                    </div>
                                </div>
                            )
                        })}

                    </div>
                </React.Fragment>
                }
                <div className='text-right'>
                    {id && <div className='btn mr10' onClick={this.deleteItem}>delete</div>}
                    <div className={`btn ${isSaving ? 'btndisabled' : ''}`} onClick={this.onSubmit}>{id ? 'update' : 'save'}</div>
                </div>
                <Addonmodal toastmessage={this.props.toastmessage} />
            </div>
        );
    }
}
function mapStateToProps({ auth: { token }, page: { individualItem: { data = {} }, itemLoader = false, updatedData = {} } }) {
    return { token, itemData: data, itemLoader, updatedData }
}

export default connect(mapStateToProps, { updatePageData, getIndividualItem, resetPageStore })(IndiaofIdeas);
